// ====================================
// Customize breadcrumbs
// ====================================

/*
* TABLE OF CONTENTS
*
* TOOL ============
* MIXIN STYLE ============
* MAIN CONTAINER ============
* hero-zone
* review-box
* compare-box
* best-selling-car
* most-read-box
* video-zone-homepage
* special-car-box
* car-life-box
* f1-global-box
* f1-local-box
* f1-table
* photo-zone-homepage
* CATEGORY PAGE ============
* SPECTIFY PAGE ============
*/

// > TOOL ============
.hidden {
    display: none !important;
}

#__bs_notify__ {
    display: none !important;
}

a {
    transition: color .3s ease-in-out;
}


// > MIXIN STYLE ============
@mixin box-heading-style-1() {
    font-size: 3.2rem;
    font-weight: bold;
    text-transform: uppercase;
}

@mixin fix-img-ratio() {
    .story__thumb {
        >a {
            >img {
                width: 100%;
                height: auto;

            }
        }
    }
}


// > MAIN CONTAINER ============

// > hero-zone
.hero-zone {
    position: relative;
    // height: 620px;
    height: 0;
    width: 100%;
    padding-bottom: 35%;
    overflow: hidden;
    min-width: 1140px;

    .wrap {
        height: 100%;
    }

    .bx-pager {
        display: flex;
        justify-content: center;
        position: absolute;
        bottom: 24px;
        width: 100%;

        .bx-pager-item {
            width: 150px;
            margin-right: 16px;
            background-color: rgba(white, 0.3);
            position: relative;

            a {
                display: flex;
                justify-content: flex-start;
                align-content: flex-start;
                height: 3px;
                text-indent: -999em;
                transition: ease-in-out;
                background-color: transparent;
                z-index: 1;

                span {
                    width: 0%;
                    display: block;
                }
            }

            .bx-pager-overlay {
                z-index: 0;
                height: 3px;
                background-color: #fff;
                position: absolute;
                top: 0;
                opacity: 0;
            }
        }
    }

    .bx-viewport {
        // height: 100% !important;
    }

    .item {
        // height: 620px;
        padding: 40px 0px;
        display: flex;
        align-items: center;
        position: relative;
        height: 100%;

        @media screen and (min-width: 1281px) {
            padding: 40px 0 80px;
        }

        @media screen and (min-width: 1661px) {
            padding: 80px 0 160px;
        }

        .photo-bg,
        .video-bg {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: auto;
            z-index: 0;
            // -ms-transform: translateX(-50%) translateY(-50%);
            // -moz-transform: translateX(-50%) translateY(-50%);
            // -webkit-transform: translateX(-50%) translateY(-50%);
            // transform: translateX(-50%) translateY(-50%);
        }

        .photo-bg {
            width: 100%;
            height: auto;
        }

        &.has-black-mask {
            &:before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                width: 100%;
                background-color: black;
                opacity: 0.35;
                z-index: 10;
            }
        }

        .container {
            position: relative;
            z-index: 20;
        }
    }

    .float-left {
        display: flex;
        flex-flow: column wrap;
        justify-content: center;
    }

    .rank-1-stories {
        .story {
            width: 100%;

            &__thumb {
                position: absolute;
            }

            &__heading {
                a {
                    display: flex;
                    flex-flow: column wrap;

                    span {
                        display: block;
                        color: #fff;
                        // text-align: justify;
                    }

                    .font-size-1 {
                        font-weight: normal;
                        font-size: 40px;

                        @media screen and (max-width: 1280px) {
                            font-size: 32px;
                        }
                    }

                    .font-size-2 {
                        font-size: 50px;
                    }

                    &:hover {
                        color: #fff;
                        text-decoration: underline;
                    }
                }
            }

            &__summary {
                margin-top: 20px;
                width: 470px;
                @include font-style(16, 22, normal);
                color: #fff;

                @media screen and (max-width: 1280px) {
                    display: none;
                }
            }

            &__react {
                color: #fff;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                margin-top: 2.4rem;
                font-size: 1.6rem;

                .share {
                    margin-left: 1rem;
                }

                a {
                    color: #fff;

                    &:hover {
                        color: grey;
                    }
                }
            }
        }
    }

    .cta {
        width: 500px;
        margin-top: 40px;
        display: flex;
        justify-content: space-between;

        a {
            width: 240px;
            height: 40px;
            display: block;
            border: 2px solid white;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 1.6rem;
            transition: all .3s ease-in-out;
        }

        .btn-style-1 {
            background-color: #fff;
            color: black;

            &:hover {
                color: #fff;
                background-color: transparent;
            }
        }

        .btn-style-2 {
            color: #fff;
            background-color: transparent;
            transition: all .3s ease-in-out;

            &:hover {
                background-color: #fff;
                color: black;
            }
        }
    }

    .rank-2-stories {
        @include fix-img-ratio;
        width: 365px;
        float: right;

        .story {
            margin-top: 15px;

            //for image ratio
            &:first-child {
                margin-top: 0;
            }

            &__thumb {
                box-shadow: 0 0 15px rgba(0, 0, 0, .15);
            }

            @media screen and (min-width: 1661px) {
                margin-top: 30px;
            }

            &.story--tilt-4 {
                @media screen and (max-width: 1280px) {
                    height: 90px;

                    .story__thumb {
                        width: 80px;
                        height: 60px;

                        &>a {
                            &>img {
                                height: 100%;
                                width: auto;
                            }
                        }
                    }

                    .story__heading,
                    .story__meta {
                        left: 90px;
                        width: calc(100% - 110px);
                    }

                    .story__meta {
                        top: 67px;
                    }
                }
            }
        }
    }
}

// > review-box
.review-box {
    width: 755px;
    margin-bottom: 40px;

    .box-heading {
        @include box-heading-style-1;
    }

    .box-content {
        margin-top: 20px;
    }

    .top-stories {
        display: flex;
        justify-content: space-between;

        .rank-1-stories {
            .story {
                width: 370px;
                height: 405px;
                position: relative;

                &__thumb {
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    z-index: 10;
                    box-shadow: 0 0 10px rgba(black, .15);
                    overflow: visible;

                    a {
                        &:after {
                            content: '';
                            width: 370px;
                            height: 405px;
                            position: absolute;
                            left: -20px;
                            top: -15px;
                            z-index: 20;
                            border: 4px solid $primary;
                            transition: all .3s ease-in-out;
                        }
                    }
                }

                &__header {
                    padding: 20px;
                    width: 260px;
                    position: absolute;
                    bottom: 0;
                    left: 20px;
                    background-color: #fff;
                    z-index: 20;
                }

                &__summary {
                    width: 230px;
                    margin-top: 10px;
                    position: relative;
                    left: 40px;
                    z-index: 20;
                }

                &__meta {
                    width: 230px;
                    margin-top: 10px;
                    position: relative;
                    left: 0px;
                    z-index: 20;
                }

                &:hover {
                    .story__thumb {
                        a {
                            transform: none;

                            &::after {
                                left: 0px;
                                top: 0px;
                            }
                        }
                    }
                }
            }
        }

        .rank-2-stories {
            width: 370px;
            display: flex;
            justify-content: space-between;
            flex-flow: column wrap;
        }
    }

    .bottom-stories {
        .rank-2-stories {
            display: flex;
            justify-content: space-between;
            flex-flow: row wrap;

            .story {
                margin-top: 15px;
            }

            .story:nth-child(2n + 1) {
                width: 350px;
                margin-left: 20px;
            }

            .story:nth-child(2n) {
                width: 370px;
            }
        }
    }
}

// > compare-box
.compare-box {
    width: 320px;

    .box-heading {
        @include box-heading-style-1;
    }

    .box-content {
        margin-top: 35px;
        @include fix-img-ratio;

        .item-compare {
            display: flex;
            justify-content: space-between;
            background-color: #fff;
            margin-top: 15px;
            position: relative;

            &:first-child {
                margin-top: 0;
            }

            &:before {
                content: '';
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 85px 32px 0 32px;
                border-color: #f0f0f0 transparent transparent transparent;
                position: absolute;
                left: calc(50% - 32px);
                top: 0;
                z-index: 10;
            }

            &:after {
                content: 'VS';
                font-style: italic;
                font-weight: bold;
                font-size: 22px;
                color: #C0C0C0;
                position: absolute;
                left: calc(50% - 17px);
                top: 5px;
                z-index: 20;
            }
        }

        .story {
            width: 160px;
            height: 170px;
            overflow: hidden;

            &__thumb {
                height: 85px;
            }

            &__heading {
                font-size: 1.4rem;
                padding: 15px 10px;
            }

            &:nth-child(1) {
                .story__heading {
                    padding-right: 5px;
                }
            }

            &:nth-child(2) {
                .story__heading {
                    padding-left: 5px;
                    text-align: right;
                }
            }
        }

        .fyi--300 {
            margin: 20px auto 0;
        }
    }
}

// > best-selling-car
.top-view {
    // width: 320px;

    .box-heading {
        @include box-heading-style-1;
        // padding-left: 20px;
    }

    .box-content {
        margin-top: 20px;
        margin-left: -60px;
        padding: 40px 0 0;
        position: relative;
        overflow: hidden;

        &:before {
            content: '';
            width: 350px;
            background-color: #fff;
            position: absolute;
            top: 0;
            left: 60px;
            bottom: 11px;
        }

        .item {}

        .story {
            @include clearfix();
            position: relative;
            margin-top: 30px;
            padding: 0 10px 0 85px;
            transition: all 0.3s ease-in-out;

            &:first-child {
                margin-top: 0;
            }

            &__thumb {
                width: 100px;
                height: 75px;
                float: left;
                box-shadow: 0 0 15px rgba(black, .15);
                transition: all 0.3s ease-in-out;
                transform-origin: right top;

                >a {
                    transform: none;
                }
            }

            &__heading {
                margin-left: 115px;
            }

            &__price {
                margin-top: 4px;
                margin-left: 115px;
                font-weight: bold;
                color: $secondary;
            }

            &__position {

                position: absolute;
                left: 45px;
                top: 24px;
                width: 30px;
                height: 30px;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: #0044E1;
                font-size: 18px;
                font-weight: 700;
                color: #fff;
                transition: all .3s ease-in-out;
                z-index: 3;
            }
        }

        .story:hover {

            .story__thumb {
                transform: scale(1.45, 1.33);
            }

            .story__position {
                left: 10px;
                top: 25px;
                width: 50px;
                height: 50px;
                font-size: 24px;
            }
        }

        .user-controls {
            display: none;

            .btn-prev {}

            .btn-next {}
        }

        .bx-viewport {
            padding-bottom: 40px;
        }

        .bx-pager {
            display: flex;
            justify-content: center;
            margin-left: 50px;
            margin-top: -11px;
            position: relative;

            .bx-pager-item {
                a {
                    display: flex;
                    justify-content: center;
                    align-content: flex-start;
                    margin: 0 5px;
                    padding-top: 2px;
                    width: 22px;
                    height: 22px;
                    color: #fff;
                    font-weight: bold;
                    background-color: $gray-400;
                }

                .active {
                    background-color: $primary;
                }
            }
        }
    }

    .fyi--300 {
        margin: 24px auto 0;
    }
}

.top-car {
    @extend .top-view;
    width: 100%;

    .box-content {
        padding: 40px 0 10px;
        margin: 20px 0 0;

        &::before {
            width: 100%;
            left: 0;
            bottom: 22px;
        }

        .wrap {
            display: -webkit-flex;
            display: -moz-flex;
            display: -ms-flex;
            display: -o-flex;
            display: flex;
            justify-content: center;
            flex-direction: row;
            flex-wrap: wrap;
            flex-flow: row wrap;
            align-content: flex-end;
        }

        .story {
            padding-left: 60px;
            width: 50%;
            margin-top: 0;
            margin-bottom: 30px;

            &:hover {
                .story__position {
                    left: 0;
                }
            }

            &__meta {
                margin-left: 115px;
                margin-top: 5px;
            }
        }
    }
}

// > most-read-box
.most-read-box {
    width: 755px;
    max-height: 525px;
    overflow: hidden;

    .box-heading {
        @include box-heading-style-1;
        padding-left: 20px;
        display: flex;
    }

    .box-content {
        margin-top: 20px;
        position: relative;

        .story {
            @include clearfix();
            width: 250px;
            height: 435px;
            margin-right: -10px !important;
            padding-top: 80px;
            display: flex;
            flex-flow: column wrap;
            position: relative;

            &:before {
                content: '';
                height: 395px;
                width: 220px;
                background-color: #fff;
                position: absolute;
                top: 20px;
                left: 15px;
                box-shadow: 0 0 5px rgba(black, .1);
            }

            &__thumb {
                width: 220px;
                height: 165px;
                margin: 0px 15px 0;
                position: relative;
                transition: all .3s ease-in-out;
            }

            &__heading {
                margin: 20px 25px 0;
                position: relative;
            }

            &__meta {
                position: absolute;
                bottom: 40px;
                left: 25px;
                color: $secondary;
                font-weight: bold;
            }

            &__position {
                position: absolute;
                left: 90px;
                top: 10px;
                width: 50px;
                height: 50px;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: #242424;
                font-size: 24px;
                font-weight: bold;
                color: #fff;
                transition: all .3s ease-in-out;
            }

            &:hover {
                .story__thumb {
                    transform: scale(1.15);
                }

                .story__position {
                    background-color: $primary;
                    top: 0px;
                }
            }

            &--sponsor {
                .story__meta {
                    color: #6c757d;
                    font-weight: normal;
                }
            }
        }

        .user-controls {
            position: absolute;
            top: -55px;
            right: 0;
            width: 60px;
            display: flex;
            justify-content: space-between;

            span {
                width: 24px;
                height: 24px;
                display: flex;
                justify-content: center;
                align-content: center;
                position: relative;

                i {
                    font-size: 20px;
                    color: $gray-500;
                    margin-top: 2px;
                    transition: color .3s ease-in-out;

                }

                a {
                    position: absolute;
                    width: 24px;
                    height: 24px;
                    opacity: 0;
                }

                &:hover {
                    i {
                        color: $gray-800;
                    }
                }
            }

            .btn-prev {}

            .btn-next {}
        }

        .bx-pager {
            display: flex;
            justify-content: center;
            position: absolute;
            position: relative;
            margin-left: 15px;

            .bx-pager-item {
                a {
                    display: flex;
                    margin: 0 5px;
                    width: 14px;
                    height: 14px;
                    border-radius: 50%;
                    border: 2px solid $gray-400;
                    text-indent: -999em
                }

                .active {
                    background-color: $primary;
                    border-color: $primary;
                }
            }
        }

    }
}

