// > special-car-box
.special-car-box {
    .box-heading {
        @include box-heading-style-1;
        display: flex;
        justify-content: center;
    }

    .box-content {
        margin-top: 3px;
        @include fix-img-ratio;

        .story {
            width: 560px;
            height: 300px;
            position: relative;

            &__thumb {
                position: absolute;
                width: 100%;
                height: 100%;
                z-index: 10;
                box-shadow: 0 0 10px rgba(black, .15);
            }

            &__header {
                width: 345px;
                position: absolute;
                bottom: 0;
                left: 20px;
                z-index: 20;
                background-color: #fff;
                padding: 20px;
            }

            &__heading {
                position: relative;
            }

            &__summary {
                display: none;
                width: 300px;
                margin-top: 10px;
                position: relative;
                left: 40px;
                z-index: 20;
            }

            &__meta {
                margin-top: 10px;
            }

            &:hover {
                &:after {
                    left: -0px;
                    top: -0px;
                }

                .story__thumb {
                    a {
                        transform: scale($rate-zoom);
                    }
                }
            }
        }
    }
}

// > car-life-box
.car-life-box {
    width: 560px;

    .box-heading {
