                    margin-top: 10px;
                }
            }
        }

        .rank-2-stories {
            padding-top: 25px;
            margin-top: 25px;
            border-top: 1px solid #000;

            .story {
                position: relative;
                margin-top: 30px;
                padding-left: 20px;

                &:first-child {
                    margin-top: 0;
                }

                &:before {
                    content: '';
                    width: 8px;
                    height: 8px;
                    position: absolute;
                    left: 0;
                    top: 5px;
                    background-color: $primary;
                }
            }
        }
    }
}

// > f1-global-box
.f1-global-box {
    width: 795px;

    .box-heading {
        @include box-heading-style-1;
    }

    .box-content {
        margin-top: 3px;

        .rank-1-stories {
            @include fix-img-ratio;

            .story {
                width: 795px;
                height: 450px;
                position: relative;

                &__thumb {
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    z-index: 10;
                    box-shadow: 0 0 10px rgba(black, .15);
                    overflow: visible;

                    a {
                        &:after {
                            content: '';
                            position: absolute;
                            left: -20px;
                            top: -15px;
                            z-index: 20;
                            border: 4px solid $primary;
                            transition: all .3s ease-in-out;
                            width: 795px;
                            height: 450px;
                        }

                        >img {
                            width: 100%;
                            height: 100%;
                        }
                    }
                }

                &__header {
                    padding: 20px;
                    width: 380px;
                    position: absolute;
                    bottom: 0;
                    left: 20px;
                    background-color: #fff;
                    z-index: 20;
                }

                &__summary {
                    width: 340px;
                    margin-top: 10px;
                    position: relative;
                    z-index: 20;
                }

                &__meta {
                    width: 300px;
                    margin-top: 10px;
                    position: relative;
                    z-index: 20;
                }

                &:hover {
                    .story__thumb {
                        a {
                            transform: none;

                            &::after {
                                left: 0px;
                                top: 0px;
                            }
                        }
                    }
                }
            }
        }

        .rank-2-stories {
            display: flex;
            justify-content: space-between;
            margin-top: 15px;

            .story {
                width: 375px;
            }

            .story:nth-child(1) {
                margin-left: 23px;
            }
        }
    }
}

