// > f1-table
.f1-table {
    background-color: #2E2E2E;

    .box-heading {
        height: 75px;
        background-color: #242424;
        @include box-heading-style-1;
        padding-left: 35px;

        a {
            display: flex;
            height: 100%;
            align-items: center;
            color: #fff;
        }
    }

    .box-content {}

    .panel-wrap {
        @include clearfix();
    }

    .panel-title {
        display: flex;
        flex-flow: row wrap;
        margin-top: 10px;
        padding-left: 20px;

        a {
            height: 36px;
            display: flex;
            padding: 0 $spacer-4;
            color: white;
            text-transform: uppercase;
            font-weight: bold;
            opacity: .7;
            transition: all .3s ease-in-out;

            span {
                height: 36px;
                display: flex;
                align-items: center;
                border-bottom: 5px solid transparent;
            }

            &:hover,
            &.is-active {
                opacity: 1;

                span {
                    border-bottom-color: white;
                }
            }
        }
    }

    .panel-content {
        padding: 25px 15px 30px;

        .f1-table-info {
            display: flex;
            font-size: 1.2rem;

            .date-time {
                span {
                    color: #878787;
                    margin-right: 10px;
                }

                time {
                    color: #fff;
                }
            }

            .track {
                margin-left: 40px;

                span:nth-child(1) {
                    color: #878787;
                    margin-right: 10px;
                }

                span:nth-child(2) {
                    color: $secondary;
                    font-weight: bold;
                }
            }
        }

        .f1-table-detail {
            margin-top: 15px;
            background-color: #fff;

            thead {
                background-color: #E9E9E9;
                text-transform: uppercase;

                th:nth-child(1),
                th:nth-child(4),
                th:nth-child(5) {
                    text-align: center;
                }
            }

            tbody {
                th {
                    font-weight: bold;
                    text-align: center;
                }

                td:nth-child(4),
                td:nth-child(5) {
                    text-align: center;
                }

                tr:nth-child(n+5) {
                    display: none;
                }
            }

            &.is-active {

                tr:nth-child(n+5) {
                    display: table-row;
                }
            }
        }

        .panel {
            display: none;

            &.is-active {
                display: block;
            }

            button {
                width: 36px;
                height: 36px;
                border-radius: 50%;
                margin: -30px auto 0px;
                display: flex;
                justify-content: center;
                align-items: center;
                border: none;
                background-color: #F6F6F6;
                box-shadow: 0px 0px 5px rgba(black, .5);
                cursor: pointer;

                i {
                    font-size: 22px;
                    color: $gray-700;
                }

                .fa-angle-up {
                    display: none;
                }

                .fa-angle-down {
                    display: block;
                }

                &.expand {
                    .fa-angle-up {
                        display: block;
                    }

                    .fa-angle-down {
                        display: none;
                    }
                }

                &:hover {
                    background-color: #ccc;
                }
            }
        }
    }
}

