            .first-story {
                .story__thumb {
                    height: 300px;
                }
            }

            .second-story {
                .story__thumb {
                    height: 300px;
                }

                .story__meta {}
            }

            .third-story {
                .story__thumb {
                    height: 300px;
                }

                .story__meta {}
            }
        }
