// ====================================
// Category page's style
// ====================================

.category-page {
    .site-header {
        margin-bottom: 40px;
    }

    .hero-zone {
        .rank-1-stories {
            .story {
                width: auto;

                &__heading {
                    letter-spacing: 0.355rem;

                    a {
                        span {}
                    }
                }
            }
        }

        .story__summary,
        .cta {}
    }

    .category-section {
        margin-top: 70px;

        .row>.col-12 {
            display: flex;
            justify-content: space-between;
        }
    }

    .box {
        margin-bottom: 24px;
    }

    .top-box {
        .box-heading {
            @include box-heading-style-1;
        }

        .box-content {
            margin-top: 3px;

            .rank-1-stories {
                @include fix-img-ratio;

                .story {
                    width: 795px;
                    height: 450px;
                    position: relative;

                    &__thumb {
                        position: absolute;
                        width: 100%;
                        height: 100%;
                        z-index: 0;
                        box-shadow: 0 0 10px rgba(black, .15);
                        overflow: visible;

                        a {
                            &:after {
                                content: '';
                                position: absolute;
                                left: -20px;
                                top: -15px;
                                z-index: 20;
                                border: 4px solid $primary;
                                transition: all .3s ease-in-out;
                                width: 795px;
                                height: 450px;
                            }

                            >img {
                                width: 100%;
                                height: 100%;
                            }
                        }

                    }

                    &__header {
                        padding: 20px;
                        width: 380px;
                        position: absolute;
                        bottom: 0;
                        left: 20px;
                        background-color: #fff;
                        z-index: 20;
                    }

                    &__summary {
                        width: 340px;
                        margin-top: 10px;
                        position: relative;
                        z-index: 20;
                    }

                    &__meta {
                        width: 300px;
                        margin-top: 10px;
                        position: relative;
                        z-index: 20;
                    }

                    &:hover {
                        .story__thumb {
                            a {
                                transform: none;

                                &::after {
                                    left: 0px;
                                    top: 0px;
                                }
                            }
                        }
                    }
                }
            }

            .rank-2-stories {
                display: flex;
                justify-content: space-between;
                margin-top: 15px;

                .story {
                    width: 375px;
                }

                .story:nth-child(1) {
                    margin-left: 23px;
                }
            }
        }
    }

    .sidebar-box {
        display: flex;
        flex-flow: column wrap;

        .box-heading {
            @include box-heading-style-1;
        }

        .box-content {
            @include clearfix();
            background-color: #fff;
            margin-top: 50px;
            padding: 0 15px;

            .rank-1-stories {
                @include fix-img-ratio;

                .story {
                    margin-top: -47px;

                    &__thumb {
                        width: 290px;
                        height: 160px;
                        margin: 0px auto 0;
                        box-shadow: 0 0 15px rgba(black, .15);
                    }

                    &__heading {
                        margin-top: 15px;
                    }

                    &__meta {
                        margin-top: 10px;
                    }

                    &__summary {
                        margin-top: 10px;
                    }
                }

            }

            .rank-2-stories {
                border-top: 1px solid #000;
                margin-top: 30px;
                padding-top: 30px;
                padding-bottom: 30px;

                .story {
                    margin-top: 25px;

                    &:first-child {
                        margin-top: 0;
                    }

                    &__thumb {
                        width: 95px;
                        height: 70px;
                        float: left;
                    }

                    &__heading {
                        margin-left: 110px;
                    }

                    &__meta {
                        margin-top: 10px;
                    }

                    &__summary {
                        margin-top: 10px;
                    }
                }
            }
        }
    }

    .listing-box {
        .story {
            background-color: #fff;

            &__thumb {
                float: right;
                width: 205px;
                height: 185px;
            }

            &__heading {
                width: calc(100% - 205px);
                padding: 24px 24px 6px;
            }

            &__meta {
                padding-left: 24px;
                padding-right: 24px;
                width: calc(100% - 205px);
            }

            &__summary {
                padding: 24px;
                width: calc(100% - 205px);
            }

            &.story {
                margin-top: 24px;
            }
        }

        .box-footer {
            margin-top: 30px;
        }

        .view-more {
            text-align: center;

            .btn-outline-primary {
                padding-left: 7rem;
                padding-right: 7rem;
                border-width: 4px;
                color: $primary;
                border-color: $primary;

                &:hover {
                    color: white;
                    background-color: $primary;
                }
            }
        }
    }

    .most-read-box {
        width: 810px;
        margin-left: -15px;
        margin-right: -15px;
        margin-bottom: 40px;
    }
}

.box {
    &-brand {
        .story {
            display: flex;
            align-items: center;
            margin-bottom: 32px;

            &__thumb {
                width: 120px;
                height: 120px;
                border: 1px solid #adb5bd;
                margin-right: 16px;
                float: left;
            }

            &__heading {
                a {
                    font-size: 24px;
                }
            }
        }

        .pagination {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            transform: translateY(50%);
        }
    }
}

.card-grid {
    margin-top: 20px;

    .col-4 {
        margin-bottom: 32px;
    }

    .story {
        height: 100%;
        background-color: #fff;

        &__thumb {
            width: 100%;
            height: 157px;
        }

        &__header {
            padding: 16px;
        }
    }
}